html {
  height     : 100%;
  font-size  : 14px;
  font-weight: 400;
}

body {
  margin     : 0;
  margin     : 0px;
  font-size  : 14px;
  font-family: AvenirNextLTPro-Regular, OpenSans-Light, Lato, sans-serif;
  line-height: 1.2;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.react-overlay-loader-spinner:before {
  content         : "";
  box-sizing      : border-box;
  position        : absolute;
  top             : 50%;
  left            : 50%;
  width           : 40px;
  height          : 40px;
  margin-top      : -30px;
  margin-left     : -20px;
  border-radius   : 50%;
  border          : 3px solid #eee;
  border-top-color: #07d;
  animation       : react-overlay-loader-spinner 0.8s linear infinite;
}

.modal-backdrop,
.modal-backdrop.fade.in {
  opacity   : 0.8 !important;
  filter    : alpha(opacity=80) !important;
  background: #000;
}

.modal-content {
  height: unset !important;
}


@font-face {
  font-family: 'AvenirNextLTPro-Regular';
  src        : local('AvenirNextLTPro-Regular'), url('./fonts/AvenirNextLTPro-Regular.woff'),
    /* Modern Browsers */
    local('AvenirNextLTPro-Regular'), url('./fonts/AvenirNextLTPro-Regular.ttf') format('truetype');
  /* Safari, Android, iOS */
  font-style: normal;
}

* {
  -webkit-box-sizing    : border-box;
  box-sizing            : border-box;
  -webkit-font-smoothing: antialiased;
}




h3 {
  margin-top: 0;
}

input[type="radio"] {
  margin: 3px;
}

input[type="radio"][disabled]+label {
  color: #999;
}

select {
  border    : 0px;
  width     : 100%;
  height    : 110px;
  outline   : 0;
  overflow-y: auto;
}

option:checked {
  background: linear-gradient(0deg, #009ee3 0%, #009ee3 100%);
}

.app {
  width         : 100%;
  display       : flex;
  flex-direction: column;
  overflow      : hidden;
}


button,
input,
optgroup,
select,
textarea {
  color : inherit;
  font  : inherit;
  margin: 0;
}

button,
select {
  text-transform: none;
}

button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  cursor            : pointer;
}

.btn-default,
button {
  background: none;
}

button {
  overflow: visible;
  border  : none;
}

.basic-btn {
  padding               : 0 20px;
  display               : inline-block;
  cursor                : pointer;
  font-size             : 14px;
  line-height           : 22px;
  height                : 25px;
  border-radius         : 4px;
  font-weight           : 600;
  -webkit-font-smoothing: antialiased;
  background-color      : #0095e0;
  border                : 1px solid #0095e0;
  color                 : #fff;
}

.basic-btn.outline {
  background-color: transparent;
  color           : rgb(68, 68, 68);
  border-color    : rgb(119, 119, 119)
}

.basic-btn.pale {
  background-color: #fff;
  color           : #777;
  border-color    : #e2e2e2;
}

.basic-btn.ml10 {
  margin-left: 10px;
}

.basic-btn.disabled,
.basic-btn:disabled {
  cursor: default;
}

.basic-btn.disabled,
.basic-btn.disabled:hover,
.basic-btn:disabled,
.basic-btn:disabled:hover {
  background-color: #eee;
  border-color    : #eee;
  color           : #fff;
}

.powered-by {
  position: fixed;
  bottom  : 21px;
  left    : 16px;
}

.powered-by a {
  text-decoration: none;
  color          : #e2e2e2;
  font-size      : 10px;
  margin         : 0px;
  padding        : 0px;
}

.powered-by a:hover {
  text-decoration: none;
  color          : #444;
}

.hide-me {
  display: none;
}

.trashcan-admin-bar {
  display          : flex;
  height           : 45px;
  padding          : 0px 20px 0px 15px;
  -webkit-box-pack : justify;
  justify-content  : space-between;
  -webkit-box-align: center;
  align-items      : center;
  border           : none;
  border-bottom    : 1px solid rgb(226, 226, 226);
  background       : #fff;
}

.trashcan-top-bar {
  display          : flex;
  height           : 45px;
  padding          : 0px 20px 0px 15px;
  -webkit-box-pack : justify;
  justify-content  : space-between;
  -webkit-box-align: center;
  align-items      : center;
  border           : none;
  border-bottom    : 1px solid rgb(226, 226, 226);
  background       : #fff;
}

.trashcan-admin-bar-message {
  font-weight: bold;
  width      : 110px;
  flex       : 0.61;
}

a.dropdown-item {
  border-radius: 3px;
  align-content: center;
  display      : flex;
}

a.dropdown-item:active {
  background-color: unset;
}

.trashcan-dropdown-title {
  line-height: 45px;
  padding    : 0px 16px;
}

.trashcan-admin-bar .dropdown .button,
.trashcan-admin-bar .dropdown .button:hover,
.trashcan-admin-bar .dropdown .button:focus {
  display               : inline-block;
  cursor                : pointer;
  font-size             : 14px;
  font-weight           : 600;
  -webkit-font-smoothing: antialiased;
  color                 : rgb(119, 119, 119);
  line-height           : 45px;
  height                : 45px;
  border                : none;
  border-left           : 1px solid rgb(226, 226, 226);
  border-radius         : 0px;
  margin                : 0px;
}

.trashcan-admin-bar .dropdown .button:hover {
  color: rgb(51, 51, 51);
}

.trashcan-admin-bar .dropdown .dropdown-menu {
  position          : absolute;
  top               : 44px;
  right             : 0px;
  border            : 1px solid rgb(226, 226, 226);
  background        : #fff;
  padding           : 8px;
  -webkit-box-shadow: 1px 2px 4px 1px rgb(0 0 0 / 25%);
  box-shadow        : 1px 2px 4px 1px rgb(0 0 0 / 25%);
  flex-direction    : column;
  display           : flex;
  max-height        : 75vh;
  overflow          : hidden;
  overflow-y        : auto;
}

.trashcan-admin-bar .dropdown .dropdown-menu ul {
  flex-direction      : column;
  display             : flex;
  padding-inline-start: 0px
}

.trashcan-admin-bar .dropdown .dropdown-menu ul a {
  color          : #777;
  height         : 30px;
  line-height    : 30px;
  text-decoration: none;
  justify-content: flex-start;
  align-items    : center;
}

.trashcan-admin-bar .dropdown .dropdown-menu ul a:hover {
  color          : rgb(51, 51, 51);
  text-decoration: none;
}

.trashcan-admin-bar .dropdown .dropdown-menu ul a:active {
  color          : rgb(51, 51, 51);
  text-decoration: none;
}

.basic-btn-title {
  padding: 0px 16px 0px 24px;
}

.basic-btn-label {
  color: #0095e0;
}

