/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow   : inset 0 0 5px #6f6f6f;
  border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background   : #d0d0d0;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(153, 153, 153);
}

.pointer {
  cursor: pointer;
}

.reflex-container.vertical>.reflex-splitter {
  cursor: ew-resize;
}