.selected {
  border-color      : #009ee3;
  -webkit-box-shadow: 0 0 0 2px #009ee3 !important;
  box-shadow        : 0 0 0 2px #009ee3 !important;
  border-radius     : 2px;
}

.image {
  width              : 100%;
  background-size    : contain;
  height             : 100%;
  background-repeat  : no-repeat;
  background-position: center;
}

.image-container {
  width     : 160px;
  height    : 240px;
  display   : inline-block;
  border    : 1px solid rgb(226, 226, 226);
  background: #fff;
  margin    : 6px;
}

.image-container .bottom-area {
  padding: 4px;
}

.image-container .top-area {
  background: #aaa;
}

.image-container .bottom-area p {
  padding    : 0px;
  margin     : 0px;
  font-size  : 10px;
  line-height: 12px;
  width      : 100%;
  overflow   : hidden;
}

.image-container .bottom-area .image-text-header {
  color : #000;
  margin: 7px 0 0 0;
}

.image-container .bottom-area .image-text {
  margin       : 0px 0 13px 0;
  text-overflow: ellipsis;
  overflow     : hidden;
  white-space  : nowrap;
  color        : #777;
}

.image-text-tooltip {
  font-size    : 0.8em;
  margin-bottom: 5px;
}

.tooltip {
  background-color: red;
}

.image-text-header-tooltip {
  font-size: 1em;
}

.image-text-tooltip {
  margin-bottom: 4px;
}