.dropleft {
  position: unset !important;
}

.dropdown {
  position: unset !important;
}

.left-pane {
  padding         : 10px;
  background-color: #F5F6F8;
  border-bottom   : 1px solid darkgray;
  border-right    : 1px solid darkgray;
}

.right-pane {
  padding         : 10px;
  background-color: #F5F6F8;
  border-bottom   : 1px solid darkgray;
  border-right    : 1px solid darkgray;
}

.pane-content {
  display         : flex;
  flex-wrap       : wrap;
  height          : 100%;
  padding         : 10px;
  background-color: #F5F6F8;
}

.selected-folder {
  background-color: red;
}

.modal-70w {
  width    : 50%;
  height   : 40%;
  max-width: 60%;
}

.modal-content {
  height : 100%;
  padding: 10px;
}

.modal-text {
  /* margin-left: 0px;
  margin-top : 0px; */
  overflow-wrap: anywhere;
  margin-bottom: 10px;
}

.user-selection {
  display    : flex;
  align-items: center;
  flex       : 9;
}

.nothing-found {
  align-items    : center;
  display        : flex;
  justify-content: center;
  height         : 100%;
}

.modal-content {
  padding: 0;
}

/* .modal-header {
  padding-top: 5px;
  font-weight: bold;

} */

.model-title-text {
  font-weight  : bold;
  margin-bottom: 5px;
}

.custom-control-input:checked~.custom-control-label:before {
  background-color: #009ee3;
}

.custom-control.custom-checkbox {
  display: flex;
  align-items: center;
}


