.gallery-items {
  display  : flex;
  flex-wrap: wrap;
}

.gallery-box .status-bar {
  position      : fixed;
  left          : 0px;
  right         : 0px;
  bottom        : 0px;
  height        : 35px;
  border-top    : 1px solid rgb(226, 226, 226);
  background    : #fff;
  display       : flex;
  width         : 100%;
  flex-direction: row;
  align-items   : center;
  padding       : 0px 16px;
}

.gallery-box .status-bar button {
  margin: 0px 16px;
}

.gallery-box .status-bar form {
  margin: 0px;
}

.gallery-box .status-bar .btn-toolbar {
  width         : 50%;
  display       : flex;
  flex-direction: row;
  align-items   : center;
}

