.float-right {
  display      : flex;
  float        : right;
  margin       : 0;
  position     : absolute;
  top          : 50%;
  right        : 0px;
  -ms-transform: translateY(-50%);
  transform    : translateY(-50%);
}

.float-left {
  display      : flex;
  float        : left;
  margin       : 0;
  position     : absolute;
  top          : 50%;
  left         : 22px;
  -ms-transform: translateY(-50%);
  transform    : translateY(-50%);
}

.select-link {
  color        : #cecece;
  padding-right: 20px;
  cursor       : pointer;
}

.select-link:hover {
  color: cyan;
}

.custom-control-label:after,
.custom-control-label:before {
  top: 1px;
}

.custom-control-label {
  cursor     : pointer;
  user-select: none;
}

.right {
  justify-content: flex-end;
}
.paginationText {
  margin-left:auto;
  margin-right:auto;
  width: 8em;
  text-align: center;
}
.pagination {
  padding-top:10px;
  margin-left:auto;
  margin-right:auto;
  width: 8em;
  margin-bottom: 4px;
}
.pagination > li {
  display: inline-block;
  padding-left: 0;
  background: #fff;
  margin: 0px 2px;
}
.pagination > li.selected {
  border-color: transparent;
  box-shadow: none !important;
  background: #009ee3;
  color: white;
}

.pagination > li {
  list-style: none;
}
.pagination > li > a,
.pagination > li > span {
  position: relative;
  float: left;
  padding: 6px 12px;
  
}


/* Style the active class (and buttons on mouse-over) */
.pagination > li > a:hover {
  background-color:  #0095e0;
  color: white;
}
.pagination > li:first-child > a,
.pagination > li:first-child > span {
  margin-left: 0;
    padding: 0px;
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
  display: none!important;
}
.pagination > li:last-child > a,
.pagination > li:last-child > span {
  border-bottom-right-radius: 4px;
  margin-right: 0;
  padding: 0px!important;
  border-top-right-radius: 4px;
  display: none!important;
}